"use client"
import Link from 'next/link';
import { FaUser, FaEnvelope, FaHeart, FaShoppingCart, FaSearch } from 'react-icons/fa';

const Header = () => {
  const handleSearch = () => {
    alert("Search functionality to be implemented!");
  };

  return (
    <header className="flex items-center justify-between px-4 py-3 shadow-md bg-white">
      {/* Logo Section */}
      <div className="flex items-center space-x-3">
        <div className="w-10 h-10 bg-blue-100 flex justify-center items-center rounded-md">
          <FaShoppingCart className="text-blue-500 text-xl" />
        </div>
        <h1 className="text-xl font-bold text-blue-500">Brand</h1>
      </div>

      {/* Search Section */}
      <div className="flex items-center border rounded-md overflow-hidden">
        <input
          type="text"
          placeholder="Search"
          className="px-4 py-2 w-64 outline-none"
        />
        <select className="border-l px-2 py-2 outline-none">
          <option>All category</option>
          <option>Electronics</option>
          <option>Fashion</option>
          <option>Home</option>
        </select>
        <button
          onClick={handleSearch}
          className="bg-blue-500 text-white px-4 py-2 hover:bg-blue-600 flex items-center"
        >
          <FaSearch className="mr-2" /> Search
        </button>
      </div>

      {/* Profile and Icons Section */}
      <div className="flex items-center space-x-6 text-gray-500">
        <div  className="cursor-pointer">
          <Link href="/profile">
          <FaUser className="text-lg" />
          <p className="text-sm">Profile</p>
          </Link>
        </div>
        <div className="cursor-pointer">
          <Link href="/contact">          <FaEnvelope className="text-lg" />
          <p className="text-sm">Message</p>
        </Link>
        </div>
        <div  className="cursor-pointer">
          <Link href="/orders">
          <FaHeart className="text-lg" />
          <p className="text-sm">Orders</p>
          </Link>
        </div>
        <div className="cursor-pointer">
          <Link href="/cart">          <FaShoppingCart className="text-lg" />
          <p className="text-sm">My cart</p>
          </Link>
        </div>
      </div>
    </header>
  );
};

export default Header;
